.card__event__ {
  &container {
    @apply bg-white shadow w-full;
    @apply border-2 rounded-lg border-yellow-500;
  }

  &header {
    @apply bg-gray-100 p-2;
    @apply text-center font-bold text-xl;
    @apply rounded-t-lg border-b shadow;
    @apply flex justify-between items-center;
  }

  &logo {
    @apply w-16 h-16;
    @apply p-1 rounded-lg;
    @apply object-scale-down;
    @apply bg-blueGray-300;
  }

  &title {
    @apply text-center w-full;
  }

  &body {
    @apply py-2 p-4;
  }
  &footer {
    @apply rounded-b-lg border-t-2 py-2 px-4;
  }
}
