.header__ {
  &container {
    @apply bg-gradient-to-b from-primary-600 via-primary-500 to-primary-400;
    @apply dark:from-blueGray-700 dark:via-blueGray-800 dark:to-blueGray-900;
    @apply py-5 transition-colors duration-500;
  }
  &button {
    @apply absolute text-4xl z-10 text-white dark:text-gray-300;
    @apply top-2 lg:top-5 drop-shadow-xl;
    @apply right-3  lg:right-10 xl:right-32 2xl:right-60;
  }

  &content {
    @apply top-0 bottom-0 left-0 right-0 relative;
    @apply flex flex-col sm:flex-row justify-around items-center;
    @apply bg-transparent  w-full h-full;

    img {
      @apply rounded-full shadow-xl;
      @apply border-4 border-primary-500 dark:border-blueGray-600;
      @apply w-36 h-36 sm:w-40 sm:h-40 md:w-44 md:h-44 lg:w-48 lg:h-48;
    }

    .name {
      @apply text-center px-3;
      @apply text-white dark:text-gray-300;

      h2 {
        @apply text-3xl;
      }
      h1 {
        @apply text-xs italic;
      }
    }
  }
}
