.technologies__ {
  &container {
    @apply pb-10 pt-5;
    @apply bg-gradient-to-b from-primary-100 via-primary-200 to-primary-400;
    @apply dark:from-blueGray-700 dark:via-blueGray-800 dark:to-blueGray-900;
    @apply border-t-2 border-primary-500 pb-10  text-primary-500 dark:text-primary-600;
  }

  &title {
    @apply text-2xl text-center;
  }

  &subtitle {
    @apply text-center pb-3 pt-1;
  }

  &card__ {
    &container {
      @apply grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 2xl:grid-cols-8 gap-3;
      @apply px-3;

      .card {
        @apply px-2 pt-4 pb-3 bg-white dark:bg-gray-300;
        @apply transition-all duration-300 transform scale-95 md:scale-100 hover:scale-95;
        @apply shadow-none md:shadow-lg hover:shadow-none;
        @apply border rounded-lg;
        @apply flex flex-col justify-between items-center;

        .image {
          @apply h-24 w-24 mb-2;

          img {
            @apply w-full h-full object-contain;
          }
        }
        label {
          @apply mt-2;
        }
      }
    }
  }
}
