@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: ZenDots;
  src: url(./assets/font/ZenDots.ttf);
  font-display: fallback; /* Define how the browser behaves during download */
}

#root {
  font-family: ZenDots;
  @apply min-h-screen bg-primary-100 dark:bg-blueGray-700;
  @apply transition-colors duration-300;
}

button {
  outline: none !important;
}

div.size__container {
  @apply container w-full;
  @apply max-w-sm sm:max-w-xl md:max-w-xl lg:max-w-3xl xl:max-w-5xl 2xl:max-w-7xl;
}

.bg-color {
  @apply bg-primary-700 dark:bg-primary-900;
}
.border-color {
  @apply border-primary-700 dark:border-primary-900;
}
