footer {
  @apply bg-gradient-to-t from-primary-600 via-primary-500 to-primary-400;
  @apply dark:from-blueGray-700 dark:via-blueGray-800 dark:to-blueGray-900;
  @apply text-white dark:text-gray-300;
  @apply py-5;

  .title {
    @apply text-center text-3xl mb-4;
  }

  .social__networks {
    @apply flex justify-around items-center;
    @apply text-5xl;

    a {
      @apply transition-all duration-300;
      @apply transform scale-100 hover:scale-125;
      @apply hover:shadow-xl;
    }
  }

  .copy {
    @apply text-sm text-center mt-3;
  }
}
